<script>
import AnalyzeGraph from './components/Analyze.vue';
import OverviewGraph from './components/Overview.vue';
// import TableComponent from "@/components/Table.vue";
export default {
  name: 'App',
  components: {
    AnalyzeGraph,
    OverviewGraph
    // TableComponent
},  
  data () {
    return {
      activeIndex: '1',
      activeName: 'OverviewTab',
      option4Overview: {},
      option4Analyze: {},
      tableData: [],
      MaxBlocknumber: 15365903,
      value1: true,
      MinBlocknumber: 15265903,
      jump: 0,
      token: 'Eth',
      blockThreshold: 7200,
      value: 5,
      accounts: ['0xc611952d81e4ecbd17c8f963123dec5d7bce1c274'],
      blockNumber: 16540597,
                txTimer: undefined,
                blockTime: 'Unknown',
                dashboard: {
                    "hour": 734,
                    "minute": 46,
                    "seconds": 1
                }
    }
  },
  created () {
  },
  mounted () {
  },
  methods: {
    getInputStyle: function() {
      if (this.accounts.length > 1) {
        return "width: 170px; margin-right: 10px"
      }
      return "width: 200px" 
    },
    addAccount: function(event) {
      this.accounts.push("0xc611952d81e4ecbd17c8f963123dec5d7bce1c274")
    },
    deleteAccount: function(event, index) {
      if (this.accounts.length <= 1) {
        return 
      }
      this.accounts.splice(index, 1)
    },
    search: function () {
      this.$http.post('/data/analyze/', {account: "0xi8l4rv7k4y9buf72i0lh6yw1y12aq8mwduanw940", jump: 3}, {headers: {'Content-Type': 'application/json'}}, {emulateJSON: true}).then(function (res) {
                    // this.token = res.body.token
                    this.code = 200
                    // this.activeNumber = 3
                    this.option4Analyze = {
                      "nodes": res.body.nodes,
                      "edges": res.body.edges,
                    }
                }, function (err) {
                    this.code = err.status
                    this.message = err.body.message
                })
      this.$http.post('/data/overview/', {account: "0xi8l4rv7k4y9buf72i0lh6yw1y12aq8mwduanw940", jump: 3}, {headers: {'Content-Type': 'application/json'}}, {emulateJSON: true}).then(function (res) {
          // this.token = res.body.token
          this.code = 200
          this.option4Overview = {
            "nodes": res.body.nodes,
            "edges": res.body.edges,
            "json": res.body.json
          }
      }, function (err) {
          this.code = err.status
          this.message = err.body.message
      })
    }
  }
}
</script>
