<template>
    <div style="width: 100%">
      <div class="diy-legend" v-show="!empty">
        <div style="width: 100%;text-align:center;font-size: large;font-weight: 700;">Statistics</div>
        <li>Number of Nodes:{{ nodesNumber }}</li>
        <li>Number of edges:{{ edgesNumber }}</li>
        <!-- <div style="height: 20px;display: flex">
          <div style="font-size: medium;font-weight: 700;"><i class="fa fa-address-card-o"></i><span style="margin-left: 2px">Accounts: {{ nodesNumber }}</span></div>
      </div>
      <div style="height: 20px;display: flex;width: 100%">
          <div style="font-size: medium;font-weight: 700"><i class="fa fa-balance-scale"></i><span style="margin-left: 2px">Transactions: {{ edgesNumber }}</span></div>
      </div> -->
      <div id="pie"></div>
      </div>
      <div class="choice" v-show="!empty">
        <div class="each-choice" style="background: lightgray">
          <img src="https://raw.githubusercontent.com/Liuyushiii/img/master/ETH.png"><span style="font-size: small; font-weight: 700; margin-left: 10px">ETH</span>
        </div>
        <div class="each-choice">
          <img src="https://raw.githubusercontent.com/Liuyushiii/img/master/USDC.png"><span style="font-size: small; font-weight: 700; margin-left: 7.5px">USDC</span>
        </div>
      </div>
        <el-empty description="No Data" style="width: 100%" v-show="empty"></el-empty>
        <div id="overview" style="width: 1200px;height: 800px;" v-show="!empty"></div>
    </div>
</template>
<!-- <script type="text/javascript" src="/static/js/echarts.js"></script>
<script type="text/javascript" src="https://assets.pyecharts.org/assets/echarts.min.js"></script> -->
<script>
    export default {
        name: "OverviewGraph",
        props: ['option'],
        data() {
            return {
                empty: true,
                chart: undefined,
                nodesNumber: 0,
                edgesNumber: 0
            }
        },
        mounted() {
        },
        watch: {

            option(newOption) {
                // console.log(newOption)
                this.empty = false
                this.Draw(newOption["nodes"], newOption["edges"])
                this.pie(newOption["json"])
            }

            },
        methods: {
          pie: function(json) {
            var chart = this.$echarts.init(
            document.getElementById('pie'), 'white', {renderer: 'canvas'});
            chart.clear()
            json  = {
    "animation": true,
    "animationThreshold": 2000,
    "animationDuration": 1000,
    "animationEasing": "cubicOut",
    "animationDelay": 0,
    "animationDurationUpdate": 300,
    "animationEasingUpdate": "cubicOut",
    "animationDelayUpdate": 0,
    "color": [
    '#9EC9FF',
        '#5AC8A6',
   //     '#5D7092',
        '#F6C816',
        '#E8684A',
        '#6DC8EC',
        '#9270CA',
        '#FF9D4D',
        '#269A99',
        '#FF99C3',
    ],
    "series": [
        {
            "type": "pie",
            "clockwise": true,
            "data": json,
            "radius": [
                "40%",
                "75%"
            ],
            "center": [
                "50%",
                "50%"
            ],
            "label": {
                "show": true,
                "position": "inner",
                "margin": 8,
                "formatter": "{b}:{c}"
            },
            "rippleEffect": {
                "show": true,
                "brushType": "stroke",
                "scale": 2.5,
                "period": 4
            }
        }
    ],
    "legend": [
        {
            "data": [
                "1-5",
                "6-10",
                ">10"
            ],
            "selected": {},
            "show": true,
            "left": "0%",
            "bottom": "0%",
            // "orient": "vertical",
            // "padding": 5,
            "itemGap": 10,
            "itemWidth": 20,
            "itemHeight": 14
        }
    ],
    "tooltip": {
        "show": true,
        "trigger": "item",
        "triggerOn": "mousemove|click",
        "axisPointer": {
            "type": "line"
        },
        "showContent": true,
        "alwaysShowContent": false,
        "showDelay": 0,
        "hideDelay": 100,
        "textStyle": {
            "fontSize": 14
        },
        "borderWidth": 0,
        "padding": 5
    },
    // "title": [
    //     {
    //         // "text": "Degree Distribution",
    //         "padding": 5,
    //         "itemGap": 10
    //     }
    // ]
}
            chart.setOption(json)
          },
            Draw: function (nodes, edges) {
                if (this.chart != undefined) {
                    this.chart.destroy()
                }
                this.nodesNumber = nodes.length
                this.edgesNumber = edges.length
                const tooltip = new this.$antv.Tooltip({
    offsetX: 10,
    offsetY: 10,
    // the types of items that allow the tooltip show up
    // 允许出现 tooltip 的 item 类型
    itemTypes: ['node', 'edge'],
    // custom the tooltip's content
    // 自定义 tooltip 内容
    getContent: (e) => {
      const outDiv = document.createElement('div');
      // outDiv.style.width = 'fit-content';
      outDiv.style.padding = '0px 40px 0px 20px';
      if (e.item.getType() == 'node'){
        outDiv.innerHTML = `
        <h4>Account Information</h4>
        <ul>
            <li>Address: ${e.item.getModel().id}</li>
        </ul>`;
      }
      else if (e.item.getType() == 'edge'){
        outDiv.innerHTML = `
        <h4>Transaction Information</h4>
        <ul>
          <li>From: ${e.item.getModel().source}</li>
          <li>To: ${e.item.getModel().target}</li>
          <li>Value: ${e.item.getModel().value}</li>
        </ul>
        `;
      }
      return outDiv;
    },
    shouldBegin: (e) => {
      // console.log(e.target);
      let res = true;
      switch (e.item.getModel().id) {
        case '1':
          res = false;
          break;
        case '2':
          if (e.target.get('name') === 'text-shape') res = true;
          else res = false;
          break;
        case '3':
          if (e.target.get('name') !== 'text-shape') res = true;
          else res = false;
          break;
        default:
          res = true;
          break;
      }
      return res;
    },
  });
  const container = document.getElementById('overview');
        // 配置容器属性
        const width = 1200;
        const height = 800;
        const default_node_size = 10;
        const color_list = [
        '#9EC9FF',
        '#5AC8A6',
   //     '#5D7092',
        '#F6C816',
        '#E8684A',
        '#6DC8EC',
        '#9270CA',
        '#FF9D4D',
        '#269A99',
        '#FF99C3',
        ];
        const graph = new this.$antv.Graph({
            container: 'overview',
            width,
            height,
            // 布局
            layout: {
                // type: 'force',
                // preventOverlap: true,
                // 节点距离
                nodeSpacing: 15,
                clustering: 'false'
            },
            // 默认节点大小
            defaultNode: {
                type: 'circle',
                size: default_node_size,
                // color: 'black',
                style: {
                    fill: '#9EC9FF',
                    lineWidth: 1,
                    stroke: ''
                  },
                labelCfg: {
                  position: 'top',
                  style:{
                    fontSize: 15,
                    fontWeight: 700
                  }
                }
            },
            defaultEdge: {
                //type: 'quadratic',
                style: {
                  lineWidth: 1.5,
                  endArrow: true,
                },
                labelCfg: {
                  autoRotate: true,
                },
            },
            // 配置插件
            plugins: [tooltip],
            modes: {
                default: ['drag-canvas', 'drag-node', 'zoom-canvas'],
              },
        });
        
        function get_degree(degree){
            if (degree < 10){
              return default_node_size + 2 * degree
            }
            return default_node_size + 1 * degree
        }

        function get_label(id, degree){
          if (degree > 5){
            var firstChars = id.substring(0,5)
            var lastChars = id.substring(id.length-3)
            return firstChars + '...' + lastChars
          }
          return ''
        }

        function get_color(degree){
          if (degree >= 5 && degree <= 10){
            return color_list[1]
          }
          else if (degree > 10){
            return color_list[2]
          }
          return color_list[0]
        }

        nodes.forEach(node=>{
          // 配置节点名称
          var id = node.id;
          // 配置节点大小
          var degree = node.degree
          // node.size = get_degree(degree)
          node.cluster = 0
          node.label = get_label(id, degree)
          node.style ={
            fill: get_color(degree)
          }
        });
        graph.data({
          nodes: nodes,
          edges: edges
          });

        // this.$antv.Util.processParallelEdges(data.edges);

        graph.render();

        graph.on('node:click', (evt) => {
          const { item } = evt;
          item.toFront();
          // graph.setItemState(item, 'selected', true);
        });
                        }
        },
        // mounted() {
        // }
    }
</script>
<style>
.choice {
  position: absolute;
  left: 10px;
  width: 75px;
  height: 57.5px;
  border-left: 1px solid lightgray;
  border-right: 1px solid lightgray;
  border-top: 1px solid lightgray;
}
.each-choice {
  width: 100%;
  height: 28.75px;
  border-bottom: 1px solid lightgray;
  line-height: 28.75px;
  display: flex;
  align-items: center;
  text-align: center;
}
.each-choice img {
  width: 20px;
  margin-left: 7.5px;
}
.diy-legend {
  position: absolute;
  bottom: 5px;
  left: 10px;
  width: 240px;
  height: 240px;
  border: 1px solid lightgray;
  border-radius: 1rem;
  padding-top: 1%;
  /* padding-bottom: 2%; */
  padding-left: 1%;
  padding-right: 1%;
  z-index: 1000;
  background: white;
}
#pie {
  position: absolute;
  bottom: 0px;
  left: 30px;
  width: 180px;
  height: 160px;
  z-index: 1000;
  
  /* border: 1px solid lightgray; */
}
.diy-legend li {
  margin-left: 15px
}
</style>