<template>
    <div style="width: 100%">
      <div class="choice" v-show="!empty">
        <div class="each-choice" style="background: lightgray;border-bottom: 1px solid white;">
          <img src="https://raw.githubusercontent.com/Liuyushiii/img/master/ETH.png"><span style="font-size: small; font-weight: 700; margin-left: 10px">ETH</span>
        </div>
        <div class="each-choice" style="background: lightgray">
          <img src="https://raw.githubusercontent.com/Liuyushiii/img/master/USDC.png"><span style="font-size: small; font-weight: 700; margin-left: 7.5px">USDC</span>
        </div>
      </div>
        <el-empty description="No Data" style="width: 100%" v-show="empty"></el-empty>
        <div id="analyze" style="width: 1200px;height: 800px;" v-show="!empty"></div>
    </div>
</template>
<!-- <script type="text/javascript" src="/static/js/echarts.js"></script> -->
<script>
    export default {
        name: "AnalyzeGraph",
        props: ['option'],
        data() {
            return {
                empty: true,
                chart: undefined,
            }
        },
        mounted() {
        },
        watch: {

            option(newOption) {
                console.log(newOption)
                this.empty = false
                this.Draw(newOption["nodes"], newOption["edges"], newOption["dic"])
            }

            },
        methods: {
            Draw: function (nodes, edges) {
                if (this.chart != undefined) {
                    this.chart.destroy()
                }
                const tooltip = new this.$antv.Tooltip({
    offsetX: 10,
    offsetY: 10,
    // the types of items that allow the tooltip show up
    // 允许出现 tooltip 的 item 类型
    itemTypes: ['node', 'edge'],
    // custom the tooltip's content
    // 自定义 tooltip 内容
    getContent: (e) => {
      const outDiv = document.createElement('div');
      // outDiv.style.width = 'fit-content';
      outDiv.style.padding = '0px 40px 0px 20px';
      if (e.item.getType() == 'node'){
        outDiv.innerHTML = `
        <h4>Account Information</h4>
        <ul>
            <li>Address: ${e.item.getModel().id}</li>
            <li>x: ${e.item.getModel().x}</li>
            <li>y: ${e.item.getModel().y}</li>
        </ul>`;
      }
      else if (e.item.getType() == 'edge'){
        outDiv.innerHTML = `
        <h4>Transaction Information</h4>
        <ul>
          <li>From: ${e.item.getModel().source}</li>
          <li>To: ${e.item.getModel().target}</li>
          <li>Value: ${e.item.getModel().value}</li>
        </ul>
        `;
      }
      return outDiv;
    },
    shouldBegin: (e) => {
      // console.log(e.target);
      let res = true;
      switch (e.item.getModel().id) {
        case '1':
          res = false;
          break;
        case '2':
          if (e.target.get('name') === 'text-shape') res = true;
          else res = false;
          break;
        case '3':
          if (e.target.get('name') !== 'text-shape') res = true;
          else res = false;
          break;
        default:
          res = true;
          break;
      }
      return res;
    },
  });
        const container = document.getElementById('analyze');
        // 配置容器属性
        const width = 1200;
        const height = 800;
        const default_node_size = 10;
        const grid = new this.$antv.Grid();
        const graph = new this.$antv.Graph({
            container: 'analyze',
            width,
            height,
            // 默认节点大小
            defaultNode: {
                type: 'circle',
                size: default_node_size,
                // color: 'black',
                style: {
                    fill: '#DA5914',
                    lineWidth: 0,
                  },
                labelCfg: {
                  position: 'right'
                }
            },
            defaultEdge: {
                //type: 'quadratic',
                style: {
                  endArrow: {
                    path: this.$antv.Arrow.vee(7, 8, 5),
                    d: 0,
                    fill: 'black',
                    arrowPosition: 0.5
                    },
                  //endArrow: true,
                  lineWidth: 1,
                  // fill: '#f00',
                  stroke: '#778899',
                },
                labelCfg: {
                  autoRotate: true,
                },
            },
            // 配置插件
            plugins: [tooltip],
            modes: {
                default: ['drag-canvas', 'drag-node', 'zoom-canvas'],
              },
        });

        nodes.forEach(node=>{   
          // node.style= {
          //   fill: node.color,
          // };
          if (node.label == 'null'){
            node.label = '';
          }
          // node.labelCfg={
          //   style:{
          //     fontSize: 10,
          //     fontWeight: 800
          //   }
          // }
        });
        graph.data({
            nodes: nodes,
            edges: edges
            });
        
        graph.render();

        graph.on('node:dragend', (e) => {
          console.log(e.item.getModel().id+','+e.x+','+e.y)
        });
                        }
        },
        // mounted() {
        // }
    }
</script>
<style>
.choice {
  position: absolute;
  left: 10px;
  width: 75px;
  height: 57.5px;
  border-left: 1px solid lightgray;
  border-right: 1px solid lightgray;
  border-top: 1px solid lightgray;
}
.each-choice {
  width: 100%;
  height: 28.75px;
  border-bottom: 1px solid lightgray;
  line-height: 28.75px;
  display: flex;
  align-items: center;
  text-align: center;
}
.each-choice img {
  width: 20px;
  margin-left: 7.5px;
}
</style>